.hero {
	padding-bottom: 1rem;
	
	@include media-breakpoint-up(lg) {
		padding-bottom: 0.5rem;
	}

	> .container,
	> [class*="container-"] {
		padding: 0;
	}
}

.hero-media {
	margin-bottom: -3rem;

	@include media-breakpoint-up(md) {
		margin-bottom: -5rem;
	}

	img {
		width: 100%;
	}
}

.hero-content {
	position: relative;
	overflow: hidden;
	background: $white;
	padding: 1.5rem $container-padding-x 0;
	margin-right: $container-padding-x;

	.hero-center & {
		margin-left: $container-padding-x;
	}

	@include media-breakpoint-up(md) {
		width:  calc(66.66667% - 1rem);

		.hero-center & {
			width: calc(66.66667% - 2rem);
			margin: 0 auto;
		}
	}

	@include media-breakpoint-up(lg) {
		width: calc(66.66667% - 2rem);
		padding: 3rem 3rem 1.5rem;
		
		.hero:not(.hero-center) & {
			margin-left: $container-padding-x;
		}
	}
}

// Overlay
@supports (display: grid) {
	@include media-breakpoint-up(md) {
		.hero-overlay {
			display: grid;
			padding-bottom: 0;

			> * {
				grid-column: col 1;
				grid-row: row 1 ;
			}

			.hero-media {
				margin-bottom: 0;

				img {
					width: 100vw;
					height: 100%;
					object-fit: cover;
				}
			}

			> .container,
			> [class*="container-"] {
				display: flex;
				padding-top: $spacer * 2;
				padding-bottom: $spacer * 2;
				flex-direction: column;
				justify-content: center;
			}

			.hero-content-trans {
				background-color: $light-trans;
			}

			.hero-content-semi-trans {
				background-color: $light-semi-trans;
			}

			&.hero-right {
				> .container,
				> [class*="container-"] {
					align-items: flex-end;
				}
			}

			&.hero-bottom {
				> .container,
				> [class*="container-"] {
					justify-content: flex-end;
				}
			}
		}
	}

	@include media-breakpoint-only(md) {
		.hero-overlay {
			> .container,
			> [class*="container-"] {
				padding-left: $container-padding-x;
				padding-right: $container-padding-x;
			}
		}
	}

	@include media-breakpoint-up(lg) {
		.hero-overlay {
			.hero-content {
				width:  calc(50% - 2rem);
			}
		}
	}
}