.lt-header {
	background-color: $gray-4;
	.nav, 
	.dropdown-menu {
		font-size: $font-size-sm;
		line-height: $line-height-sm;
	}

	.ico-person-circle-outline {
		vertical-align: middle;
		margin-right: 0.5rem;
		position: relative;
		top: -0.0625rem;
	}
}
.lt-header-nav {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.lt-header-primary {
	flex: 1 0 100%;
	order: 3;

	.dropdown-toggle {
		&::after {
			color: $gray-5;
		}

		&:hover,
		&:focus {
			&::after {
				color: currentColor;
			}
		}
	}
	.nav-link {
		padding: 1.25rem 0 1rem;
		margin: 0 0.5rem;
		border-bottom: 0.25rem solid transparent;
	}
}
.lt-header-utility {
	order: 2;
	display: flex;
	justify-content: flex-end;
	padding-right: ($container-padding-x - $nav-link-padding-x);

	
}
.lt-header-global {
	order: 1;

	.nav-link {
		color: $white !important;
		border-right: 1px solid $gray-5;
		padding: $nav-link-padding-y $container-padding-x;

		&:hover,
		&:focus {
			background-color: $gray-3;
		}

		&.active {
			color: $gray-4 !important;
			background-color: $gray-11;
			border-right: 0;
			border-top: 0.25rem solid $aqua-lighter;
			padding-top: $nav-link-padding-y - 0.25rem;
			&:not(:first-child) {
				margin-left: -1px;
			}

			&:hover,
			&:focus {
				background-color: $gray-10;
			}
		}
	}

	.dropdown-toggle:after {
		transform-origin: center;
		vertical-align: 0.25em;
	}
}

.lt-header-dropdown-col {
	//width: 20rem - ($dropdown-spacer-x * 2);
	
	//.dropdown-item {
	//	white-space: normal;
	//}

	@include media-breakpoint-up(lg) {
		width: $dropdown-min-width * 2;
		column-count: 2;
		//column-gap: $dropdown-two-col-gap;

		li {
			break-inside: avoid;
		}
	}
}

.lt-header-mobile-bar {
	font-size: 0.75rem;
	height: 3.5rem;
	border-top: 4px solid $aqua-lighter;
	.nav-link {
		padding: 0.5rem;
	}
}