.lt-footer {
	padding: 2rem 0 6rem;
}

.lt-footer-nav {
	text-transform: uppercase;
	font-size: 0.6875rem;
	line-height: 1rem;
	letter-spacing:0.05em;
	font-weight: $font-weight-bold;
}

.lt-footer-legal.nav-dark .nav-link,
.lt-footer-copyright {
	font-size: 0.75rem;
	line-height: 1rem;
	color: $gray-8;
}

.lt-footer-legal.nav-dark .nav-link {
	&:hover,
	&:focus {
		color: $white;
	}
}