.icon-social:hover,
.icon-social:focus,
.icon-social-solid {
	border-color: transparent;

	&.icon-social-facebook {
		background-color: $facebook-color;
	}
	&.icon-social-instagram {
		background-color: $instagram-color;
	}
	&.icon-social-youtube {
		background-color: $youtube-color;
	}
	&.icon-social-twitter {
		background-color: $twitter-color;
	}
	&.icon-social-tiktok {
		background-color: $tiktok-color;
	}


}

.icon-social-solid {
	&.icon-social-mail {
		background-color: $gray-3;
	}
}

.icon-social:hover,
.icon-social:focus {
	&.icon-social-mail {
		background-color: $gray-5;
	}
}

.icon-social-solid:hover,
.icon-social-solid:focus {
	&.icon-social-facebook {
		background-color: darken($facebook-color, 10%);
	}
	&.icon-social-instagram {
		background-color: darken($instagram-color, 10%);
	}
	&.icon-social-youtube {
		background-color: darken($youtube-color, 10%);
	}
	&.icon-social-twitter {
		background-color: darken($twitter-color, 10%);
	}
	&.icon-social-tiktok {
		background-color: darken($tiktok-color, 10%);
	}
	&.icon-social-mail {
		background-color: $gray-1;
	}
}
