body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

//
// Headings
//
.h1 {
  @extend h1;
}

.h2 {
  @extend h2;
}

.h3 {
  @extend h3;
}

.h4 {
  @extend h4;
}

.h5 {
  @extend h5;
}

.h6 {
  @extend h6;
}


.lead {
  @include font-size($lead-font-size);
  line-height: $lead-line-height;
  font-weight: $lead-font-weight;

  @include media-breakpoint-up(lg) {
    @include font-size($lead-font-size-lg);
    line-height: $lead-line-height-lg;
  }
}

// Type display classes
@each $display, $font-size in $display-font-sizes {
  .display-#{$display} {
    @include font-size($font-size);
    font-weight: $display-font-weight;
    line-height: $display-line-height;
  }
}

//
// Emphasis
//
.small {
  @extend small;
}

.micro {
  @include font-size($micro-font-size);
  line-height: $micro-line-height;
}

.mark {
  @extend mark;
}

//
// Lists
//

.list-unstyled {
  @include list-unstyled();
}

// Inline turns list items into inline-block
.list-inline {
  @include list-unstyled();
}
.list-inline-item {
  display: inline-block;

  &:not(:last-child) {
    margin-right: $list-inline-padding;
  }
}

.list-bulleted {
  padding-left: 1.5rem;
  li:not(:last-child) {
    margin-bottom: 0.5rem;
  }
}


//
// Misc
//

// Builds on `abbr`
.initialism {
  @include font-size($initialism-font-size);
  text-transform: uppercase;
}

//  Horizontal rules
.hr-short {
  background-color: transparent;

  &:not([size]) {
    height: 2px;
  }
  
  &::after {
    display: inline-block;
    vertical-align: top;
    content: '';
    background-color: currentColor;
    width: 60px;
    height: 2px;
  }
}



// Blockquotes
.blockquote {
  margin-bottom: $blockquote-margin-y;
  @include font-size($blockquote-font-size);

  > :last-child {
    margin-bottom: 0;
  }
}

.blockquote-footer {
  margin-top: -$blockquote-margin-y;
  margin-bottom: $blockquote-margin-y;
  @include font-size($blockquote-footer-font-size);
  color: $blockquote-footer-color;

  &::before {
    content: "\2014\00A0"; // em dash, nbsp
  }
}

// Link color inversion (for dark backgrounds)
.link-inverse,
.link-inverse-white {
	text-decoration-color: currentColor;
}

.link-inverse {
	color: $link-inverse-color;
	&:hover,
	&:focus {
		color: $link-inverse-hover-color;
	}
}
.link-inverse-white {
	color: $white;
	&:hover,
	&:focus {
		color: $link-inverse-color;
	}
}

.text-two-column,
.text-three-column,
.text-four-column {
  column-gap: $grid-gutter-width;
}

.text-two-column {
  columns: 18rem 2;
}

.text-three-column {
  columns: 18rem 3;
  column-gap: $grid-gutter-width;
}

.text-four-column {
  columns: 12rem 4;
}